let Contador = 1;
let MarcadorPagina = document.querySelector(`.marcador-pagina .enlace:nth-child(${Contador})`);
let MarcadorPagina2;
document.addEventListener('DOMContentLoaded', function(){
    let Contenedor = document.querySelector('.hero-contenido');
    Carrusel(Contenedor);
    MarcadorPagina.classList.add('seleccionado');
});
function Carrusel(contenedor){
    contenedor.addEventListener('click', function(e){
        let Atras = document.querySelector('.flecha-izquierda'),
            Adelante = document.querySelector('.flecha-derecha'),
            Hero = document.querySelector('.hero'),
            SubTitulo = document.querySelector('.ml6 .letters'),
            Tgt = e.target,
            Pagina = document.querySelector('.pagina')
        ;
        if(Tgt === Adelante){
            Hero.style.backgroundImage = "url('../img/etienne-beauregard-riverin-B0aCvAVSX8E-unsplash.png')";
            Adelante.classList.add('opacidad', 'ocultar');
            Adelante.classList.remove('cursor');
            Atras.classList.remove('opacidad', 'ocultar');
            Atras.classList.add('cursor');
            Contador++;
            MarcadorPagina2 = document.querySelector(`.marcador-pagina .enlace:nth-child(${Contador})`);
            MarcadorPagina.classList.remove('seleccionado');
            MarcadorPagina2.classList.add('seleccionado');
            if(Contador === 2){
                Pagina.innerHTML = `0${Contador} <span class="opacidad">/ 04`;
            }
        }
        else if(Tgt === Atras){
            Hero.style.backgroundImage = "url('../img/sean-pollock-PhYq704ffdA-unsplash.jpg')";
            Adelante.classList.remove('opacidad', 'ocultar');
            Adelante.classList.add('cursor');
            Atras.classList.remove('cursor');
            Atras.classList.add('opacidad', 'ocultar');
            MarcadorPagina2.classList.remove('seleccionado');
            Contador--;
            MarcadorPagina2 = document.querySelector(`.marcador-pagina .enlace:nth-child(${Contador})`);
            MarcadorPagina2.classList.add('seleccionado');
            if(Contador === 1){
                Pagina.innerHTML = `0${Contador} <span class="opacidad">/ 04`;
            }
        }
    });
}